@font-face {
  font-family: Open Sans;
  src: url("OpenSans-VariableFont_wdth,wght.b9910659.ttf") format("truetype");
  font-weight: 300 800;
  font-display: swap;
}

@font-face {
  font-family: Oranienbaum;
  src: url("Oranienbaum-Regular.a313775d.ttf") format("truetype");
  font-display: swap;
}

@font-face {
  font-family: Inter;
  src: url("Inter-VariableFont_slnt,wght.0704e0a5.ttf") format("truetype");
  font-weight: 100 900;
  font-display: swap;
}

html, body {
  margin: 0;
  padding: 0;
  font-family: Open Sans, sans-serif;
}

.navbar {
  margin: 0;
  padding: 0;
  font-size: 16px;
  font-weight: 600;
}

.text-animation {
  position: relative;

  & div {
    opacity: 0;
    white-space: nowrap;
    animation: 24s linear infinite fadeText;
    position: absolute;
    overflow: hidden;
  }

  & div:first-child {
    animation-delay: 0s;
  }

  & div:nth-child(2) {
    animation-delay: 6s;
  }

  & div:nth-child(3) {
    animation-delay: 12s;
  }

  & div:nth-child(4) {
    animation-delay: 18s;
  }
}

@keyframes fadeText {
  0%, 25% {
    opacity: 0;
  }

  6%, 21% {
    opacity: 1;
  }

  25% {
    opacity: 0;
  }
}

nav .dropdown-menu {
  border-radius: 0;
  margin-top: 5px !important;
  padding: 10px 8px 25px !important;
}

nav .dropdown-item {
  text-align: center;
  color: #fff;
  border-bottom: 1px solid #fffc;
  width: 94%;
  margin: 0 auto;
  padding: 12px 0;
  text-decoration: none;
}

#navbarDropdownArticles:after, #navbarDropdownLadiesInHR:after, #navbarDropdownWorkplaceCulture:after, #navbarDropdownEvents:after, #navbarDropdownCareers:after {
  border-top-color: #000 !important;
}

.dropdown-toggle:after {
  content: "";
  vertical-align: .255em;
  border: .3em solid #0000;
  border-top-color: currentColor;
  border-bottom: 0;
  margin-left: .255em;
  display: inline-block;
}

.dropdown-toggle.show:after, .show .dropdown-toggle:after {
  transform: rotate(180deg);
}

@media (width >= 375px) {
  #account-icon-bottom {
    display: none !important;
  }

  #account-icon-top {
    display: block !important;
  }
}

@media (width <= 940px) and (width >= 600px) {
  #date-trend {
    flex-direction: column;
    align-items: start !important;
    margin-bottom: 20px !important;
  }

  #trend {
    padding: 5px !important;
  }
}

@media (width <= 600px) and (width >= 375px) {
  #trend-animated-txt, #trend, .text-animation {
    display: none;
  }
}

@media (width <= 374px) {
  #social-icon-container, #trend-animated-txt, #trend, .text-animation {
    display: none;
  }

  #account-icon-bottom {
    display: block !important;
  }

  #account-icon-top {
    display: none !important;
  }
}
